import myMarquee from '@/components/my-marquee/my-marquee.vue'
	// import common from "../../common/jquery.js";
	var video_h = 250
	var video=null;
	
	var datalist_init=[
					[{type:10,val:10,status:[{jie:0,fang:0,val:0}]},{type:10,val:10,status:[{jie:0,fang:0,val:0}]},{isCtr:0}],
					[{type:8,val:8,status:[{jie:0,fang:0,val:0}]},{type:8,val:8,status:[{jie:0,fang:0,val:0}]},{isCtr:0}],
					[{type:7,val:7,status:[{jie:0,fang:0,val:0}]},{type:7,val:7,status:[{jie:0,fang:0,val:0}]},{isCtr:0}],
					[{type:6,val:6,status:[{jie:0,fang:0,val:0}]},{type:6,val:6,status:[{jie:0,fang:0,val:0}]},{isCtr:0}],
					[{type:5,val:5,status:[{jie:0,fang:0,val:0}]},{type:5,val:5,status:[{jie:0,fang:0,val:0}]},{isCtr:0}],
					[{type:4,val:4,status:[{jie:0,fang:0,val:0}]},{type:4,val:4,status:[{jie:0,fang:0,val:0}]},{isCtr:0}]
				];
	var jifenlist_init=[
					{jifen:10,status:0,type:0},
					{jifen:20,status:0,type:0},
					{jifen:50,status:0,type:0},
					{jifen:100,status:0,type:0},
					{jifen:200,status:0,type:0},
					{jifen:300,status:0,type:0},
					{jifen:500,status:0,type:0},
					{jifen:1000,status:0,type:0},
					{jifen:2000,status:0,type:0},
					{jifen:3000,status:0,type:0},
					{jifen:5000,status:0,type:0}
				];
	
	var jifenlist_kou=[
					{jifen:1,status:0,type:0},
					{jifen:2,status:0,type:0},
					{jifen:5,status:0,type:0},
					{jifen:10,status:0,type:0},
					{jifen:20,status:0,type:0},
					{jifen:50,status:0,type:0},
					{jifen:100,status:0,type:0},
					{jifen:200,status:0,type:0},
					{jifen:500,status:0,type:0}
				];
	
	export default {
		data() {
			return {
				marqueelist:['严正声明:斗蛐蛐作为娱乐项目,严禁参与一切赌博相关行为,如有发现作封号处理,望大家共创和谐平台'],
				popType:0,//1-红方胜利,2-蓝方胜利,3-和局,4-打赏
				datalist: datalist_init,
				jifenlist:jifenlist_init,
				dashang_type:'MT',
				dashang_val:100,
				// 允许操作的折扣数 -1代表没有可操作的
				ctr_li_index:-1,
				// 通信开关
				sockect_run:true,
				// 当前的对数
				dui:1,
				// 总对数
				total_dui:47,
				// 用户编号
				user_id:this.bus.user.userId,
				// 当前用户剩下的积分
				user_score:200,
				// 场次
				raceId:'',
				// 第几对
				currenNum:'',
				// 第行操作
				raceNumState:0,
				// raceNumId
				raceNumId:0
			}
		},
		watch: {
			// 如果 `question` 发生改变，这个函数就会运行
			popType: function (newQuestion, oldQuestion) {
			  var me = this
			  if(me.popType==1||me.popType==2||me.popType==3)
			  {
				  setTimeout(function () {
					me.popType = 0;
				  },2000);
			  }
			}
		  },
		onReady() {
			var top=0;
			var view = uni.createSelectorQuery().select(".statusbar");
			view.boundingClientRect(data_ => {
				top = data_.height;
				var view = uni.createSelectorQuery().select("#videoLayout");
				view.boundingClientRect(data => {
				console.log("节点离页面顶部的距离为" + data.height);
					this.plsyVideo(top,data.height);
				}).exec();
			}).exec();
			
			this.receiveMsg();
		},
		onLoad() {
			// common.$('.videoLayout').height(video_h);
			// var ss = uni.createSelectorQuery();
			// var mysize = ss.select('#videoLayout')
			
			// var view = uni.createSelectorQuery().select("#videoLayout");
			// view.boundingClientRect(data => {
			// console.log("节点离页面顶部的距离为" + data);
			// }).exec(); 
			
			
			// mysize.fields(mysize,function (ret) {
			// 	 
			// });
			// debugger
			// this.plsyVideo();
		},
		methods: {
			plsyVideo:function(t,h){

















			},
			getTypeName(type)
			{
				var name = '';
				switch (type){
					case 10:
						name='开盆';
						break;
					case 8:
						name='八扣';
						break;
					case 7:
						name='七扣';
						break;
					case 6:
						name='六扣';
						break;
					case 5:
						name='五扣';
						break;
					case 4:
						name='四扣';
						break;
					default:
						break;
				}
				return name;
			},
			liClick(index,i,type,item)
			{
				if(this.ctr_li_index != index)
				{
					return;
				}
				
				if(type=='red')
				{
					alert('红方'+this.getTypeName(item[0].type))
				} else if(type=='blue')
				{
					alert('蓝方'+this.getTypeName(item[1].type))
				}
			},
			startClick(index,type,item)
			{
				if(this.ctr_li_index == -1)
				{
					return;
				}
				if(this.datalist[this.ctr_li_index][2].isCtr)
				{
					uni.showToast({
						icon:'none',
						title:'你已经操作过了!!',
						duration:2000
					})
					return;
				}
				
				var side = 0;
				var point = item.jifen;
				var kou=0;
				if(type=='red')
				{
					side=1;
					kou = this.datalist[this.ctr_li_index][0].type;
					alert('红方'+this.getTypeName(this.datalist[this.ctr_li_index][0].type)+' 压'+item.jifen)
				} else if(type=='blue')
				{
					side=2;
					kou = this.datalist[this.ctr_li_index][1].type;
					alert('蓝方'+this.getTypeName(this.datalist[this.ctr_li_index][1].type)+' 压'+item.jifen)
				}
				this.sendMsg(side,kou,point)
			},
			receiveMsg() {
				if(!this.sockect_run)
				{
					return;
				}
				var this_=this;
				this.bus.request({
					url: this.bus.url+'/race/findQqRaceDetail',
					method: 'GET',
					hideLoading:true,
					data: {userId:this.user_id},
					success: res => {
						if(res.data.status=='1')
						{
							var data_ = res.data.data;
							// 收到消息的操作--有数据返回
							if(JSON.stringify(data_) != "{}")
							{
								var obj = {};
								// 投注状态 0未开始, 1-可下注,2-停止下注
								obj.bet_state=this.getPram(data_,'bet_state');	
								// 场次
								obj.raceId=this.getPram(data_,'raceId');		
								obj.lan8kou=this.getPram(data_,'lan8kou');	
								// 结果	0初始	1红方胜	2蓝方胜	3平局			
								obj.referee=this.getPram(data_,'referee');		
								obj.hongKaipen=this.getPram(data_,'hongKaipen');		
								obj.hong8kou=this.getPram(data_,'hong8kou');		
								obj.hong6kou=this.getPram(data_,'hong6kou');		
								obj.hong5kou=this.getPram(data_,'hong5kou');		
								obj.lan4kou=this.getPram(data_,'lan4kou');	
										// 第几对
								obj.currenNum=this.getPram(data_,'currenNum');	
								if(obj.currenNum=='')
								{
									obj.currenNum=0;
								}
									// 第行操作
								obj.raceNumState=this.getPram(data_,'raceNumState');	
								obj.hong4kou=this.getPram(data_,'hong4kou');	
								obj.hon7kou=this.getPram(data_,'hon7kou');
								obj.lan6kou=this.getPram(data_,'lan6kou');
								obj.lan5kou=this.getPram(data_,'lan5kou');
								obj.lan7kou=this.getPram(data_,'lan7kou');
								obj.lanKaipen=this.getPram(data_,'lanKaipen');	
								// 次场地
								obj.raceNumId=this.getPram(data_,'raceNumId');
								// 共多少对
								obj.totalRaceNum=this.getPram(data_,'totalRaceNum');
								obj.totalPoint = this.getPram(data_,'totalPoint');
								
								//设置值
								// 当前的对数
								this.dui=parseInt(obj.currenNum);
								// 总对数
								this.total_dui=parseInt(obj.totalRaceNum);
								// 当前用户剩下的积分
								this.user_score=parseInt(obj.totalPoint);
								// 场次
								this.raceId=parseInt(obj.raceId);
								// 第几对
								this.currenNum=parseInt(obj.currenNum);
								// 次场地
								this.raceNumId=parseInt(obj.raceNumId);
								
								// 投注状态 0未开始, 1-可下注,2-停止下注
								obj.bet_state=this.getPram(data_,'bet_state');
								if(obj.bet_state == '1')
								{
									// 1-可下注 设置页面哪行可以操作
									if(obj.raceNumState == '10')
									{
										this.ctr_li_index=0;
									} else{
										this.ctr_li_index=9-parseInt(obj.raceNumState);
									}
								}
							}
						}
					},
					fail: () => {
						
					},
					complete: () => {
						setTimeout(function() {
							this_.receiveMsg();
						}, 500);
					}
				});
			},
			receiveCmd(cmd){
				// 命令解析>>>>
				// 压盘操作-可操作
				// 压盘成功操作
				// 积分操作
				// 比赛结果操作
				// 开局初始化数据
			},
			// 押分
			sendMsg(side,kou,point) {
				this.bus.request({
					url: this.bus.url+'/bet/touZhu',
					method: 'GET',
					data: {
						// 用户id
						userId:	this.user_id,
						// 场次id
						raceId:this.raceId,
						// 场次对数id
						raceNumId:this.raceNumId	,
						//  1红方 2蓝方
						side:side,
						// 10开盆8八扣7七扣6六扣5五扣4四扣
						discount:kou,
						// 分数
						point:	point	
						},
					success: res => {
						if(res.data.status=='1')
						{
							// this.datalist[this.ctr_li_index][2].isCtr = 1;
						} else {
							uni.showToast({
								title: res.data.errMsg,
								duration: 2000,
								icon:'none'
							});
						}
					},
					fail: () => {
						
					},
					complete: () => {
					}
				});
			},
			dashang() {
				this.bus.request({
					url: this.bus.url+'/point/reward',
					method: 'GET',
					data: {
							// 用户id
							userId:	this.user_id,
							// 监板 MT,红方草师RT,蓝方草师BT
							teacher:this.dashang_type,
							// 分数
							point:	this.dashang_val
						},
					success: res => {
						if(res.data.status=='1')
						{
							uni.showToast({
								title: '打赏成功!',
								duration: 3000,
								icon:'none'
							});
						} else {
							uni.showToast({
								title: res.data.errMsg,
								duration: 3000,
								icon:'none'
							});
						}
					},
					fail: () => {
						
					},
					complete: () => {
						this.popType=0;
					}
				});
			},
			getPram(data,key,val)
			{
				var ret = '';
				if(val)
				{
					ret = val;
				}
				if(data[key])
				{
					ret = data[key];
				}
				return ret;
			}
		},
		onShow:function () {
			if(video)
			{
				video.play();
			}
		},
		onHide:function () {
			if(video)
			{
				video.pause();
			}
		},onUnload:function(){
			if(video)
			{
				video.close();
			}
		},components: {
			myMarquee
		}
	}
